/* 系统色板 System colors */
/* ========================== */
// 中性色 Neutral colors
$white:   #ffffff   !default;
$black:   #000000   !default;
$gray-100:#f8f9fa   !default;
$gray-200:#e9ecef   !default;
$gray-300:#dee2e6   !default;
$gray-400:#ced4da   !default;
$gray-500:#adb5bd   !default;
$gray-600:#6c757d   !default;
$gray-700:#495057   !default;
$gray-800:#343a40   !default;
$gray-900:#212529   !default;

// 基础色板 Basic colors
$red:     #dc3545   !default; // 红
$pink:    #d63384   !default; // 粉
$orange:  #fd7e14   !default; // 橙
$yellow:  #fadb14   !default; // 黄
$green:   #52c41a   !default; // 绿
$teal:    #20c997   !default; // 青
$cyan:    #17a2b8   !default; // 蓝绿
$blue:    #0d6efd   !default; // 蓝
$indigo:  #6610f2   !default; // 靛青
$purple:  #6f42c1   !default; // 紫 


/* 产品色板 Product colors */
/* ========================== */
// 功能色 Functional colors
$primary:   $blue       !default;
$secondary: $gray-600   !default;
$success:   $green      !default;
$info:      $cyan       !default;
$warning:   $yellow     !default;
$danger:    $red        !default;
$light:     $gray-100   !default;
$dark:      $gray-800   !default;

// sass map
$theme-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark,
);


/* 字体系统 Font system */
/* ========================== */
// 字体家族 Font family
$font-family-sans-serif:  -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-family-monospace:   SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-base:        $font-family-sans-serif !default;

// 字体大小 Font size
$font-size-base:    1rem                      !default; // default 16px in most browser
$font-size-lg:      $font-size-base * 1.25    !default;
$font-size-sm:      $font-size-base * 0.875   !default;
$font-size-root:    null                      !default;

// 字重 Font weight
$font-weight-lighter:   lighter               !default;  // Lighter than the inherited font weight
$font-weight-light:     300                   !default;
$font-weight-normal:    400                   !default;
$font-weight-bold:      700                   !default;
$font-weight-bolder:    bolder                !default;  // Bolder than the inherited font weight
$font-weight-base:      $font-weight-normal   !default;

// 行高 Line height
$line-height-base:    1.5   !default;
$line-height-lg:      2     !default;
$line-height-sm:      1.25  !default;

// 标题大小 Heading size
$h1-font-size:    $font-size-base * 2.5    !default;
$h2-font-size:    $font-size-base * 2      !default;
$h3-font-size:    $font-size-base * 1.75   !default;
$h4-font-size:    $font-size-base * 1.5    !default;
$h5-font-size:    $font-size-base * 1.25   !default;
$h6-font-size:    $font-size-base * 1      !default;

// 链接 Link
$link-color:              $primary                    !default;
$link-decoration:         none                        !default;
$link-hover-color:        darken($link-color, 15%)    !default;
$link-hover-decoration:   underline                   !default;

// body
$body-bg:           $white      !default;
$body-color:        $gray-900   !default;
$body-text-align:   null        !default;

// Spacing
$spacer: 1rem !default;

// 段落 Paragraphs
$paragraph-margin-bottom: 1rem !default;

// 字体其他部分 heading list hr 等等
$headings-margin-bottom:    $spacer / 2 !default;
$headings-font-family:      null !default;
$headings-font-style:       null !default;
$headings-font-weight:      500 !default;
$headings-line-height:      1.2 !default;
$headings-color:            null !default;

$display1-size:   6rem    !default;
$display2-size:   5.5rem  !default;
$display3-size:   4.5rem  !default;
$display4-size:   3.5rem  !default;

$display1-weight:       300 !default;
$display2-weight:       300 !default;
$display3-weight:       300 !default;
$display4-weight:       300 !default;
$display-line-height:   $headings-line-height !default;

$lead-font-size:      $font-size-base * 1.25 !default;
$lead-font-weight:    300 !default;

$small-font-size:       $font-size-sm !default;
$sub-sup-font-size:     0.75em !default;
$initialism-font-size:  $small-font-size !default;

$text-muted: $secondary !default;

// blockquote
$blockquote-small-color:      $gray-600 !default;
$blockquote-small-font-size:  $small-font-size !default;
$blockquote-font-size:        $font-size-base * 1.25 !default;

// hr
$hr-color:      inherit !default;
$hr-height:     1px !default;
$hr-opacity:    0.25 !default;
$hr-margin-y:   $spacer !default;

$legend-margin-bottom:    0.5rem !default;
$legend-font-size:        1.5rem !default;
$legend-font-weight:      null !default;

// mark
$mark-bg:       #fcf8e3 !default;
$mark-padding:    0.2em !default;

$dt-font-weight:        $font-weight-bold !default;
$list-inline-padding:   0.5rem !default;

$nested-kbd-font-weight: $font-weight-bold !default;

// Code
$code-font-size:    $small-font-size !default;
$code-color:        $pink !default;
$pre-color:         null !default;

// options 可配置选项
$enable-pointer-cursor-for-buttons: true !default;

// 边框 和 border radius
$border-width:    1px !default;
$border-color:    $gray-300 !default;

$border-radius:       0.25rem !default;
$border-radius-lg:    0.3rem !default;
$border-radius-sm:    0.2rem !default;

// 不同类型的 box shadow
$box-shadow-sm:           0 0.125rem 0.25rem   rgba($black, 0.075)    !default;
$box-shadow:              0 0.5rem   1rem      rgba($black, 0.15)     !default;
$box-shadow-lg:           0 1rem     3rem      rgba($black, 0.175)    !default;
$box-shadow-inset: inset  0 1px      2px       rgba($black, 0.075)    !default;

// 效果 effects
$transition-time-base: 0.15s;
$transition-effect-base: ease-in-out;

/* 按钮 Button */
/* ========================== */
// 按钮基本属性
$btn-padding-y: 0.375rem !default;
$btn-padding-x: 0.75rem !default;
$btn-font-family: $font-family-base !default;
$btn-font-size: $font-size-base !default;
$btn-font-weight: $font-weight-normal;
$btn-line-height: $line-height-base !default;

// 不同大小的按钮 padding 和 font-size
$btn-padding-y-sm: 0.25rem !default;
$btn-padding-x-sm: 0.5rem !default;
$btn-font-size-sm: $font-size-sm !default;

$btn-padding-y-lg: 0.5rem !default;
$btn-padding-x-lg: 1rem !default;
$btn-font-size-lg: $font-size-lg !default;

// 按钮边框
$btn-border-width: $border-width !default;

// 链接按钮
$btn-link-color: $link-color !default;
$btn-link-hover-color: $link-hover-color !default;
$btn-link-disabled-color: $secondary !default;

// 按钮 Radius
$btn-border-radius: $border-radius !default;
$btn-border-radius-lg: $border-radius-lg !default;
$btn-border-radius-sm: $border-radius-sm !default;

// 按钮其他
$btn-box-shadow: inset 0 1px 0 rgba($white, 0.15), 0 1px 1px rgba($black, 0.075) !default;
$btn-disabled-opacity: 0.65 !default;
$btn-transition: color $transition-time-base $transition-effect-base, 
                  background-color $transition-time-base $transition-effect-base, 
                  border-color $transition-time-base $transition-effect-base, 
                  box-shadow $transition-time-base $transition-effect-base !default;


/* 提示 Alert */
/* ========================== */
$alert-color: $white !default;
$alert-bg: $primary !default;
$alert-padding-y: 0.75rem !default;
$alert-padding-x: 1rem !default;

// alert title
$alert-title-font-size: $h6-font-size !default;
$alert-title-font-weight: $font-weight-bold !default;

// alert close icon
$alert-icon-close-size: $font-size-sm !default;

// alert content
$alert-content-font-size: $font-size-sm !default;
$alert-content-margin: $spacer * 0.2 0 0 !default;

// alert type
$alert-bg-default: $primary !default;
$alert-bg-success: $success !default;
$alert-bg-warning: $warning !default;
$alert-bg-danger: $danger !default;


/* 菜单 Menu */
/* ========================== */
// menu
$menu-border-width: $border-width !default;
$menu-border-color: $border-color !default;
$menu-box-shadow: inset 0 1px 0 rgba($white, 0.15), 
                  0 1px 1px rgba($black, 0.075) !default;
$menu-transition:  color $transition-time-base $transition-effect-base, 
                    border-color $transition-time-base $transition-effect-base !default;

// menu-item
$menu-item-padding-y: 0.5rem !default;
$menu-item-padding-x: 1rem !default;
$menu-item-active-color: $primary !default;
$menu-item-active-border-width: 2px !default;
$menu-item-disabled-color: $secondary !default;

// sub-menu
$submenu-box-shadow: 0 2px 4px 0 rgba(0,0,0,0.12),
                      0 0 6px rgba(0,0,0,0.04);

